* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: $wc;
}

html,
body {
  height: 100%;
  position: relative;
  font-family: $monsterat;
}

.scroll-disabled {
  overflow: hidden;
  position: relative;

  &:after {
    position: absolute;
    content: '';
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: #211E1B;
    z-index: 0;
    height: 200%;
    width: 100%;
  }
}

a {
  text-decoration: none;
  transition: $tr;
}

p {
  font-size: rem(24);
  line-height: rem(36);
  font-weight: 500;

  @include lg {
    font-size: rem(21);
    line-height: rem(32);
  }

  @include md {
    font-size: rem(19);
    line-height: rem(31);
  }

  @include sm {
    font-size: rem(15);
    line-height: rem(24);
  }

  @include xs {
    font-size: rem(14);
    line-height: rem(21);

    br {
      display: none;
    }
  }
}

picture {
  display: flex;
  align-items: center;
  justify-content: center;
}

img {
  width: 100%;
  max-width: 100%;
}

ul {
  padding: 0;
  margin: 0;

  li {
    list-style: none;
  }
}

input,
button {
  outline: none;
  border: none;
  background: transparent;
}

// flex footer
.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;

  .content {
    flex: 1 0 auto;
    background-image: url('../../assets/img/bg.png');
  }
}

// flex footer end


.desk {
  @include xs {
    display: none;
  }
}

.mob {
  display: none;

  @include xs {
    display: block;
  }
}

// container
.container {
  width: 100%;
  padding-right: rem(15px);
  padding-left: rem(15px);
  margin-right: auto;
  margin-left: auto;
  max-width: rem(1140px);
  position: relative;
  box-sizing: border-box;

  @include lg {
    max-width: rem(960px);
  }

  @include md {
    max-width: rem(720px);
  }

  @include sm {
    max-width: rem(540px);
  }

  @include xs {
    max-width: 100%;
  }
}


// container end


// title start
.title {
  font-size: rem(40);
  line-height: rem(49);
  font-weight: 700;
  margin-bottom: rem(30);
  background: linear-gradient(180deg, #F0C680 0%, #C38E53 100%);
  color: #d6a566;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  &--centered {
    text-align: center;
  }

  @include lg {
    font-size: rem(35);
    margin-bottom: rem(25);
    line-height: normal;
  }

  @include md {
    font-size: rem(30);
    margin-bottom: rem(20);
  }

  @include sm {
    font-size: rem(25);
    margin-bottom: rem(15);
  }

  @include xs {
    font-size: rem(24);
    line-height: rem(29);
    margin-bottom: rem(14);
  }
}

.title-white {
  font-size: rem(40);
  line-height: rem(49);
  font-weight: 700;
  margin-bottom: rem(30);

  @include lg {
    font-size: rem(35);
    margin-bottom: rem(25);
    line-height: normal;
  }

  @include md {
    font-size: rem(30);
    margin-bottom: rem(20);
  }

  @include sm {
    font-size: rem(25);
    margin-bottom: rem(15);
  }

  @include xs {
    font-size: rem(24);
    line-height: rem(29);
    margin-bottom: rem(14);
  }
}

// title end


// btn start
.btn {
  max-width: rem(350);
  width: 100%;
  min-height: rem(50);
  border-radius: 50px;
  font-size: rem(24);
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;

  @include lg {
    max-width: rem(320);
    min-height: rem(45);
  }

  @include md {
    max-width: rem(300);
    min-height: rem(45);
  }

  @include sm {
    max-width: rem(270);
    min-height: rem(43);
    font-size: rem(23);
  }

  @include xs {
    max-width: rem(191);
    min-height: rem(30);
    font-size: rem(14);
  }

  span {
    position: relative;
    z-index: 1;
  }

  &:before,
  &:after {
    position: absolute;
    content: '';
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transition: $tr;
  }

  &:before {
    background: linear-gradient(180deg, #FED78F 0%, #935323 100%);
  }

  &:after {
    background: linear-gradient(90deg, #FED78F 10%, #935323 50%);
    opacity: 0;
  }

  &:hover {
    &:before {
      opacity: 0;
    }

    &:after {
      opacity: 1;
    }
  }

}

// btn end

// distance start
.distance {
  padding: rem(40) 0;

  @include lg {
    padding: rem(30) 0;
  }

  @include md {
    padding: rem(20) 0;
  }

  @include sm {
    padding: rem(18) 0;
  }

  @include xs {
    padding: rem(17) 0;
  }
}

// distance end

// bg gradient start
.dark-gradient-bg {
  background: linear-gradient(180deg, #322D2B 0%, #231F1E 100%);
}

// bg gradient end





// social start
.social {
  ul {
    li {
      display: inline-block;
      margin-left: rem(48);

      @include md {
        margin-left: rem(20);
      }

      @include xs {
        margin-left: rem(40);
      }

      &:first-child {
        margin-left: 0;
      }

      a {
        display: flex;
        align-items: center;
        justify-content: center;
        width: rem(48);
        border-radius: 100%;

        &:hover {
          img {
            filter: brightness(150%)
          }
        }

        @include md {
          width: rem(40);
        }

        img {
          transition: $tr;
        }
      }
    }
  }
}

// social end