.partners {
  padding: rem(10) 0;
  background-color: #322D2B;

  &__wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  &__item {
    padding: rem(15) rem(10);
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    transition: $tr;
    
    &:hover {
      transform: scale(1.1);
      cursor: pointer;
      
      img {
        transition: $tr;
        filter: brightness(150%)
      }
    }


    @include xs {
      flex-basis: 33.33%;
    }

    img {
      width: 85%;
      margin: auto;

      @include sm {
        width: 100%;
      }
    }
  }
}