.offers {
  background-image: url('../../assets/img/offers-bg.png');
  background-attachment: fixed;

  &__item {
    max-width: rem(540);
    width: 100%;
    border: rem(2) solid #FFD990;
    box-shadow: 0px 0px 20px rgba(255, 217, 144, 0.2),
      inset 8px 8px 20px rgba(0, 0, 0, 0.25);
    border-radius: rem(25);
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: rem(25) rem(35) rem(40);


    @include lg {
      padding: rem(20) rem(30) rem(35);
      max-width: rem(435);
    }

    @include md {
      padding: rem(15) rem(20) rem(25);
      max-width: rem(330);
      border-radius: rem(20);
    }

    @include sm {
      padding: rem(15) rem(15) rem(20);
      max-width: rem(240);
      border-radius: rem(15);
    }

    @include xs {
      max-width: rem(290);
      margin-bottom: rem(23);
      border-radius: rem(12);
    }

  }

  &__wrap {
    display: flex;
    justify-content: space-between;

    @include xs {
      flex-direction: column;
      align-items: center;
    }
  }

  &__item-img {
    margin-bottom: rem(23);
    max-width: 70%;
  }

  &__item-desc {
    p {
      text-align: center;
    }
  }
}