.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;

  .hamburger {
    width: 60px;
    height: 45px;
    margin: 50px auto;
    transform: rotate(0deg);
    transition: .5s ease-in-out;
    cursor: pointer;
    transform: scale(0.45);
    position: fixed;
    top: -57px;
    top: -11px;
    right: -1px;
    z-index: 11;
    display: none;

    @include xs {
      display: block;
    }
  }


  .hamburger span {
    display: block;
    position: absolute;
    height: 7px;
    width: 50%;
    background: $wc;
    opacity: 1;
    transform: rotate(0deg);
    transition: .25s ease-in-out;
  }

  .hamburger span:nth-child(even) {
    left: 50%;
    border-radius: 0 9px 9px 0;
  }

  .hamburger span:nth-child(odd) {
    left: 0px;
    border-radius: 9px 0 0 9px;
  }

  .hamburger span:nth-child(1),
  .hamburger span:nth-child(2) {
    top: 0px;
  }

  .hamburger span:nth-child(3),
  .hamburger span:nth-child(4) {
    top: 18px;
  }

  .hamburger span:nth-child(5),
  .hamburger span:nth-child(6) {
    top: 36px;
  }

  .hamburger.open span:nth-child(1),
  .hamburger.open span:nth-child(6) {
    transform: rotate(45deg);
  }

  .hamburger.open span:nth-child(2),
  .hamburger.open span:nth-child(5) {
    transform: rotate(-45deg);
  }

  .hamburger.open span:nth-child(1) {
    left: 5px;
    top: 7px;
  }

  .hamburger.open span:nth-child(2) {
    left: calc(50% - 5px);
    top: 7px;
  }

  .hamburger.open span:nth-child(3) {
    left: -50%;
    opacity: 0;
  }

  .hamburger.open span:nth-child(4) {
    left: 100%;
    opacity: 0;
  }

  .hamburger.open span:nth-child(5) {
    left: 5px;
    top: 29px;
  }

  .hamburger.open span:nth-child(6) {
    left: calc(50% - 5px);
    top: 29px;
  }

}

.menu {
  padding: rem(40) rem(30) rem(60);
  background-color: #211E1B;
  position: fixed;
  top: -200%;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  transition: $tr;
  z-index: 10;

  &.open {
    top: 0;
  }

  &__wrap {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    overflow-y: scroll;
  }

  &__logo {
    max-width: rem(154);
    width: 100%;
  }

  .nav {
    margin-top: rem(50);
    overflow-y: scroll;

    ul {
      li {
        margin-bottom: rem(20);

        a {
          text-transform: uppercase;
          font-size: rem(20);
          line-height: rem(30);
          font-weight: 500;
          color: #909090;
          display: flex;
          align-items: center;

          &:hover {
            color: $wc;

            span {
              i {
                background: $wc;
              }
            }
          }

          &.active {
            font-weight: 700;
            color: $wc;
          }

          .arrow {
            display: inline-block;
            position: relative;
            width: rem(11);
            margin-top: rem(4);
            margin-right: rem(5);

            i {
              display: block;
              width: rem(8);
              height: rem(1);
              background-color: #909090;
              position: absolute;
              transition: $tr;

              &:first-child {
                transform-origin: left;
                transform: rotate(-45deg);
                left: 0;
              }

              &:last-child {
                transform-origin: right;
                transform: rotate(45deg);
                right: 0;
              }
            }
          }
        }
      }
    }
  }

  .social {
    padding-top: rem(20);

    ul {
      display: flex;
      justify-content: space-between;
    }

    a {
      max-width: rem(48);

      &:hover {
        img {
          filter: grayscale(0);
        }
      }

      img {
        filter: grayscale(100%);
        transition: $tr;
      }
    }
  }
}