.footer {
  flex: 0 0 auto;
  background-color: #292623;

  &__col {
    max-width: rem(290);
    width: 100%;

    @include md {
      max-width: rem(210);
    }

    @include sm {
      max-width: rem(180);
    }

    @include xs {
      max-width: 100%;
      display: flex;
      justify-content: center;
    }

    &:last-child {
      width: 100%;
      max-width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;

      @include sm {
        flex-direction: column;
        justify-content: center;
      }

      @include xs {
        margin-top: rem(34);
      }
    }
  }

  &__top {
    display: flex;
    align-items: center;
    padding: rem(25) 0;
    border-bottom: rem(1) solid #525252;

    @include sm {
      align-items: flex-start;
    }

    @include xs {
      flex-direction: column;
    }
  }

  &__logo {
    max-width: rem(160);
    width: 100%;

    @include xs {
      max-width: rem(130);
    }
  }

  &__contacts {
    @include sm {
      margin-bottom: rem(20);
    }

    @include xs {
      margin-bottom: rem(40);
    }

    ul {
      li {
        display: flex;
        align-items: center;
        margin-bottom: rem(30);

        @include md {
          margin-bottom: rem(20);
        }

        @include xs {
          justify-content: center;
        }

        span {
          max-width: rem(30);
          width: 100%;
          margin-right: rem(15);

          @include md {
            max-width: rem(20);
          }

          picture {
            display: flex;
          }
        }

        &:last-child {
          margin-bottom: 0;
        }


        a {
          @include xs {
            font-size: rem(14);
          }

          &:hover {
            color: #dbad6d;
          }
        }
      }
    }
  }

  .licence {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: rem(30) 0;

    @include sm {
      flex-wrap: wrap;
    }

    &__logo {
      max-width: rem(65);

      @include sm {
        order: 0;
      }

      @include xs {
        max-width: rem(50);
      }
    }

    &__organization {
      @include sm {
        order: 2;
        width: 100%;
        margin-top: rem(20);
      }

      p {
        font-size: rem(16);
        color: #909090;
        text-align: center;

        @include xs {
          font-size: rem(14);
          line-height: rem(14);
        }
      }
    }

    &__organization-logo {
      display: flex;

      @include sm {
        justify-content: center;
      }

      @include xs {
        flex-direction: column;
      }
    }

    &__organization-item {
      margin-right: rem(30);
      transition: $tr;

      &:hover {
        transform: scale(1.1);

        img {
          filter: brightness(150%)
        }
      }

      @include xs {
        margin-right: 0;
        margin-bottom: rem(6);

        img {
          transition: $tr;
          width: auto;
        }
      }

      &:last-child {
        margin-right: 0;
      }
    }

    &__age-limit {
      max-width: rem(40);
      width: 100%;

      @include sm {
        order: 1;
      }

      @include xs {
        max-width: rem(30);
      }
    }
  }
}