.hero {
  min-height: rem(800);
  padding-bottom: rem(100);
  display: flex;
  align-items: flex-end;
  background-image: url('../../assets/img/hero.png');
  background-size: cover;
  background-position: right top;
  background-repeat: no-repeat;

  @include lg {
    background-position: 50%;
  }

  @include md {
    background-position: 35%;
    min-height: rem(600);
  }

  @include sm {
    min-height: rem(550);
    background-position: 50%;

  }

  p {
    @include sm {
      font-size: rem(20);
      line-height: normal;
    }

    @include xs {
      font-size: rem(14);
      line-height: rem(21);
    }
  }

  @include xs {
    height: 300px;
    padding-bottom: 0;
    background-image: url('../../assets/img/hero-mobile1.png');
  }


  .container {
      @media screen and (max-width: 575px) and (min-width: 400px) {
        display: flex;
        justify-content: center;
      }
    }
  

  &__wrap {
    display: inline-flex;
    flex-direction: column;

    * {
      margin-bottom: rem(50);

      @include lg {
        margin-bottom: rem(30);
      }

      @include xs {
        margin-bottom: rem(20);
      }

      &:last-child {
        margin: 0;
      }
    }

    @include xs {
      margin-bottom: rem(-110);
    }
  }

  &__title {
    font-size: rem(70);
    line-height: rem(85);
    font-weight: 900;
    text-transform: uppercase;
    margin-top: 0;

    @include lg {
      font-size: rem(50);
      line-height: rem(65);
    }

    @include md {
      font-size: rem(40);
      line-height: rem(55);
    }

    @include sm {
      font-size: rem(30);
      line-height: rem(45);
    }

    @include xs {
      line-height: rem(39);
    }
  }

  .btn {
    @include xs {
      display: none;
    }
  }
}