.about {
  box-shadow: 0px 8px 50px rgba(255, 255, 255, 0.15);
  overflow: hidden;

  @include xs {
    margin-top: rem(110);
  }

  .container {
    padding-left: rem(65);

    @include lg {
      padding-left: rem(15);
    }
  }

  &__wrap {
    display: flex;
  }

  &__desc {
    position: relative;
    max-width: rem(657);
    width: 100%;
    margin-bottom: rem(70);

    @include md {
      max-width: rem(500);
    }

    @include sm {
      max-width: rem(320);
    }

    @include xs {
      margin-bottom: rem(30);
      max-width: 100%;
    }

    p {
      br {
        @include sm {
          display: none;
        }
      }
    }
  }

  &__img {
    max-width: rem(350);
    width: 100%;
    position: absolute;
    left: 100%;
    bottom: 0;
    margin-left: rem(20);

    @include lg {
      max-width: rem(250);
    }

    @include md {
      max-width: rem(190);
    }

    @include sm {
      top: 0;
    }

    @include xs {
      display: none;
    }
  }

  .benefit {
    &__wrap {
      display: flex;
      flex-wrap: wrap;

      @include xs {
        flex-direction: column;
      }
    }

    &__item-wrap {
      flex-basis: 50%;
      margin-bottom: rem(80);


      &:nth-last-child(-n+2) {
        margin: 0;

        @include xs {
          margin-bottom: rem(50);
        }
      }

      &:last-child {
        margin: 0;
      }


      @include lg {
        margin-bottom: rem(60);
      }

      @include md {
        margin-bottom: rem(40);
      }

      @include sm {
        margin-bottom: rem(30);
      }
      @include xs {
        margin-bottom: rem(45);
      }

      @include xs {
        flex-basis: 100%;
      }
    }

    &__title {
      max-width: rem(250);
      width: 100%;
      text-transform: uppercase;

      @include lg {
        font-size: rem(20);
      }

      @include md {
        font-size: rem(18);
      }

      @include sm {
        font-size: rem(16);
      }

      @include xs {
        font-size: rem(14);
        max-width: 100%;
      }
    }

    &__item {
      display: flex;
      align-items: center;
      max-width: 80%;

      @include md {
        max-width: 100%;
      }
    }

    &__img-wrap {
      position: relative;
      width: rem(160);
      height: rem(160);
      margin-right: rem(30);
      display: flex;
      align-items: center;
      justify-content: center;

      @include lg {
        width: rem(100);
        height: rem(100);
      }

      @include md {
        width: rem(80);
        height: rem(80);
        margin-right: rem(25);
      }

      @include sm {
        width: rem(70);
        height: rem(70);
        margin-right: rem(15);
      }

      @include xs {
        width: rem(50);
        height: rem(50);
      }

      &:after {
        position: absolute;
        content: '';
        background: linear-gradient(180deg, #211E1B 0%, #231F1E 100%);
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 100%;
        width: rem(160);
        height: rem(160);

        @include lg {
          width: rem(100);
          height: rem(100);
        }

        @include md {
          width: rem(65);
          height: rem(65);
        }

        @include sm {
          width: rem(55);
          height: rem(55);
        }

        @include xs {
          width: rem(45);
          height: rem(45);
        }


      }

    }

    &__img {
      text-align: center;
      position: relative;
      z-index: 1;
    }

    &__item {
      font-size: rem(24);
      font-weight: 700;
    }
  }
}