.advantages {
  overflow: hidden;

  p {
    line-height: rem(40);

    @include lg {
      line-height: rem(32);
    }

    @include md {
      line-height: rem(31);

      br {
        display: none;
      }
    }

    @include sm {
      line-height: rem(24);
    }

    @include xs {
      line-height: rem(21);
    }

  }

  &__wrap {
    display: flex;
    justify-content: space-between;

    @include md {
      flex-direction: column;
    }
  }

  &__col {
    flex-basis: 48.5%;

    @include md {
      width: 100%;
      margin-bottom: rem(30);
    }

    &--items-wrap {
      flex-basis: 49%;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: flex-end;
      position: relative;

      &:after {
        position: absolute;
        content: '';
        max-width: rem(285);
        width: 100%;
        min-height: rem(320);
        background-image: url('../../assets/img/advantage-bg.png');
        top: 0;
        left: 90%;
      }

      @include md {
        margin-bottom: 0;
        max-width: 65%;
        width: 100%;
        margin: auto;
      }

      @include sm {
        max-width: 90%;
      }

      @media screen and (max-width: 575px) and (min-width: 400px) {
        justify-content: center;
      }

      @media screen and (max-width: 400px) {
        max-width: 100%;
        justify-content: space-between;
      }
    }
  }

  &__item-wrap {
    max-width: rem(255);
    width: 100%;
    margin-bottom: rem(33);
    position: relative;
    z-index: 1;

    @include lg {
      max-width: rem(215);
    }

    @media screen and (max-width: 575px) and (min-width: 400px) {
      flex-basis: 45%;
    }

    @media screen and (max-width: 400px) {
      flex-basis: 50%;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: rem(20) rem(25);
    border: 2px solid #FFD990;
    box-sizing: border-box;
    box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.15);
    border-radius: rem(25);
    min-height: rem(204);

    @include lg {
      border-radius: rem(20);
    }

    @include md {
      border-radius: rem(15);
    }

    @include sm {
      border-radius: rem(12);
    }

    @include xs {
      min-height: rem(104);
      padding: rem(10);
      max-width: rem(130);
      min-height: rem(104);
      margin: auto;
    }
  }

  &__title {
    font-size: rem(20);
    line-height: rem(24);
    text-align: center;
    font-weight: 700;
    margin-bottom: rem(15);

    @include lg {
      font-size: rem(18);
      line-height: normal;
    }

    @include xs {
      font-size: rem(12);
      line-height: rem(15);
      margin-bottom: rem(10);
    }
  }

  &__img {
    min-height: rem(80);
    display: flex;
    align-items: center;
    justify-content: center;

    @include xs {
      min-height: rem(40);
      max-width: rem(35);
      width: 100%;
    }

    img {
      width: auto;
    }
  }
}