.join {
  background-attachment: fixed;
  background-position: bottom;
  background-size: cover;
  background-repeat: no-repeat;
  padding: rem(100) 0;

  @include lg {
    padding: rem(90) 0;
  }

  @include md {
    padding: rem(80) 0;
  }

  @include sm {
    padding: rem(70) 0;
  }

  @include sm {
    padding-bottom: rem(50);
  }




  &__offer-block {
    background: #211E1B;
    border: rem(3) solid #FFD990;
    box-sizing: border-box;
    border-radius: rem(25);
    padding: rem(40) rem(65);
    min-height: rem(288);
    position: relative;

    @include lg {
      padding: rem(30) rem(55);
      min-height: rem(270);
    }

    @include md {
      padding: rem(25) rem(50);
      min-height: auto;
      z-index: 1;
    }

    @include sm {
      padding: rem(20) rem(40);
    }

    @include xs {
      padding: rem(18);
      border-radius: rem(12);
    }
  }

  &__offer {
    &:after {
      position: absolute;
      content: '';
      max-width: rem(380);
      width: 100%;
      right: 0;
      top: rem(-30);
      min-height: rem(265);
      background-image: url('../../assets/img/join.png');
      background-repeat: no-repeat;
      background-position: center;

      @include md {
        top: rem(-60);
      }

      @include sm {
        top: rem(-60);
        right: rem(20);
        max-width: rem(300);
        min-height: rem(132);
        background-size: contain;
      }

      @include xs {
        max-width: rem(190);
      }
    }

    .title-white {
      @include xs {
        text-align: center;
        margin-bottom: 25px;
      }
    }
  }

  &__btn-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__offer-wrap {
    max-width: 70%;
    width: 100%;
    position: relative;

    @include md {
      max-width: 100%;
    }
  }
}